import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "xin",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "Xin" */ "../views/Xin.vue")
  },
  {
    path: "/demo",
    name: "demo",
    component: () => import(/* webpackChunkName: "Demo" */ "../views/Demo.vue")
  },
  {
    path: "/default/:name/:value",
    name: "default",
    component: () =>
      import(/* webpackChunkName: "Default" */ "../views/Default.vue")
  },
  {
    path: "/vuelab",
    name: "vuelab",
    component: () => import(/* webpackChunkName: "Vuelab" */ "@/views/Vuelab.vue"),
    children: []
  },
  {
    path: "/dynamic",
    name: "dynamic",
    component: () =>
      import(/* webpackChunkName: "dynamic" */ "@/components/vuecomp/dynamic.vue")
  },
  {
    path: "/render",
    name: "render",
    component: () =>
      import(/* webpackChunkName: "render" */ "@/views/Render.vue")
  },
  {
    path: "/transition",
    name: "transition",
    component: () =>
      import(/* webpackChunkName: "transition" */ "@/components/vuecomp/transitioner.vue")
  },
  {
    path: "/store",
    name: "store",
    component: () =>
      import(/* webpackChunkName: "store" */ "@/views/Store.vue")
  },
  {
    path: "/cate",
    name: "cate",
    component: () =>
      import(/* webpackChunkName: "cate" */ "@/views/Category.vue")
  },
  {
    path: "/table",
    name: "table",
    component: () =>
      import(/* webpackChunkName: "table" */ "@/views/Table.vue")
  },
  {
    path: "/map",
    name: "myMap",
    component: () =>
      import(/* webpackChunkName: "map" */ "@/views/Map.vue")
  }
];

const router = new VueRouter({
  // mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
