
function vuePlugin(Vue) {
  /**
   * 自定义指令
   */
  Vue.directive('focus', {
    inserted: (el, binding) => {
      el.focus()
      console.log('v-focus-val', binding.value)
    } 
  }),
  /**
   * 自定义过滤器
   */
  Vue.filter('toUpper', val => {
    if(/[a-z]/i.test(val)) return val.toUpperCase()
    return val
  }),
  /**
   * 自定义mixin
   */
  Vue.mixin({
    created() {
      // console.log('这是全局添加的mixin created方法')
    }
  })
}

export default vuePlugin