export default {
  namespaced: true,
  state: {
    twoFirstState: 1,
    twoSecondSate: 2
  },
  getters: {
    twoFirstGetter: state => state.twoSecondSate + 1
  },
  mutations: {
    twoFirstMutation(state, payload) {
      state.twoFirstState += payload.mount
    }
  },
  actions: {
    twoFirstAction({state, dispatch, commit}, payload) {
      commit('twoFirstMutation', payload)
    }
  }
}