export default {
  async increase({state, dispatch, commit}, payload) {
    // await dispatch('decrese')
    // commit('add', {mount: 60 + state.bill + payload.mount})
    commit('add', await dispatch('decrese'))
  },
  decrese({commit}) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        commit('add', {mount: 20})
        resolve({mount: 36})
      }, 3000)
    })
  }
}