export default {
  namespaced: true,
  state: {
    oneFirstState: 1,
    oneSecondSate: 2
  },
  getters: {
    oneFirstGetter: state => state.oneSecondSate + 1
  },
  mutations: {
    oneFirstMutation(state, payload) {
      state.oneFirstState += payload.msg
    }
  },
  actions: {
    oneFirstAction({state, dispatch, commit}, payload) {
      commit('oneFirstMutation', payload)
      // commit('two/twoFirstMutation', payload, {root: true})
      dispatch('two/twoFirstAction', payload, {root: true})
      dispatch('increase', null, {root: true})
    }
  }
}